import { useEffect, useState, useRef, useMemo } from "react";
import { createPortal } from "react-dom";
import { FixedSizeList as List } from "react-window";
import debounce from "lodash/debounce";
import Fuse from "fuse.js";
import cls from "classnames";
import { ICONS } from "../../icons/picker";
import NetIcon from "../net-icon";

interface IconPickerProps {
  icons?: string[];
  label?: string;
  value?: string;
  onChange?: (value: string) => void;
}

const IconPicker = ({
  value,
  onChange,
  label,
  icons = ICONS,
}: IconPickerProps) => {
  const [isOpen, setIsOpen] = useState(false);
  const [position, setPosition] = useState("bottom");
  const [searchTerm, setSearchTerm] = useState("");
  const [filteredIcons, setFilteredIcons] = useState(icons);
  const btnRef = useRef<HTMLButtonElement>(null);

  const fuse = useMemo(() => {
    return new Fuse(icons, {
      includeScore: true,
      threshold: 0.3,
    });
  }, [icons]);

  useEffect(() => {
    if (!isOpen) {
      return;
    }

    const close = () => setIsOpen(false);
    document.addEventListener("click", close);

    const calculatePosition = () => {
      if (btnRef.current) {
        const rect = btnRef.current.getBoundingClientRect();
        const modalWidth = 400;
        const modalHeight = 400;

        const availableSpace = {
          right: window.innerWidth - rect.right,
          left: rect.left,
          top: rect.top,
          bottom: window.innerHeight - rect.bottom,
        };

        if (availableSpace.right >= modalWidth) {
          setPosition("right");
        } else if (availableSpace.left >= modalWidth) {
          setPosition("left");
        } else if (availableSpace.bottom >= modalHeight) {
          setPosition("bottom");
        } else if (availableSpace.top >= modalHeight) {
          setPosition("top");
        } else {
          setPosition("bottom");
        }
      }
    };

    calculatePosition();

    return () => document.removeEventListener("click", close);
  }, [isOpen]);

  const handleSearch = useMemo(() => {
    return debounce((term: string) => {
      if (term === "") {
        setFilteredIcons(icons);
      } else {
        const results = fuse.search(term);
        const sortedIcons = results.map((result) => result.item);
        setFilteredIcons(sortedIcons);
      }
    }, 300);
  }, [icons, fuse]);

  useEffect(() => {
    handleSearch(searchTerm);
    return () => {
      handleSearch.cancel();
    };
  }, [searchTerm, handleSearch]);

  const getModalStyle = () => {
    if (btnRef.current) {
      const rect = btnRef.current.getBoundingClientRect();
      switch (position) {
        case "right":
          return {
            top: rect.top + window.scrollY,
            left: rect.right + window.scrollX,
          };
        case "left":
          return {
            top: rect.top + window.scrollY,
            left: rect.left - 200 + window.scrollX,
          };
        case "top":
          return {
            top: rect.top - 200 + window.scrollY,
            left: rect.left + window.scrollX,
          };
        case "bottom":
        default:
          return {
            top: rect.bottom + window.scrollY,
            left: rect.left + window.scrollX,
          };
      }
    }
    return {};
  };

  const iconsPerRow = 5;

  const renderRow = ({
    index,
    style,
  }: {
    index: number;
    style: React.CSSProperties;
  }) => {
    // Calculate the start and end index for the icons in this row
    const startIndex = index * iconsPerRow;
    const endIndex = Math.min(startIndex + iconsPerRow, filteredIcons.length);

    return (
      <div
        key={index}
        style={{
          ...style,
          display: "flex",
          justifyContent: "space-between",
          padding: "10px 0",
        }}
      >
        {filteredIcons.slice(startIndex, endIndex).map((icon) => (
          <button
            type="button"
            key={icon}
            onClick={() => onChange?.(icon)}
            className={cls("wptb-icon-picker-modal-item", {
              "wptb-selected": icon === value,
            })}
          >
            <NetIcon name={icon} />
          </button>
        ))}
      </div>
    );
  };

  return (
    <div className="wptb-icon-picker wptb-control">
      {label && <p className="wptb-icon-picker-label">{label}</p>}
      <button
        className="wptb-icon-picker-btn"
        onClick={(evt) => {
          evt.stopPropagation();
          setIsOpen(!isOpen);
        }}
        ref={btnRef}
      >
        {value ? <NetIcon name={value} /> : null}
      </button>
      {isOpen &&
        createPortal(
          <div
            className="wptb-icon-picker-modal"
            style={getModalStyle()}
            onClick={(evt) => evt.stopPropagation()}
            onPointerUp={(evt) => evt.stopPropagation()}
          >
            <input
              type="text"
              placeholder="Search"
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
            />
            <List
              className="wptb-icon-picker-modal-content"
              height={250}
              itemCount={Math.ceil(filteredIcons.length / iconsPerRow)}
              itemSize={40}
              width={250}
            >
              {renderRow}
            </List>
          </div>,
          document.body
        )}
    </div>
  );
};

export default IconPicker;
