import { createElement, useEffect, useState } from "react";
import { DownloadingLoopIcon } from "../../icons/loaders";

const CACHE = {
  icons: new Map<string, string>(),
};

const fetchSvgIcon = async (name: string, cfg: RequestInit = {}) => {
  if (CACHE.icons.has(name)) {
    return CACHE.icons.get(name);
  }
  const url = WPTB_CFG.PLUGIN_URL + "/inc/frontend/views/icons/" + name + ".svg";
  try {
    const response = await fetch(url, cfg);
    const icon = await response.text();
    CACHE.icons.set(name, icon);
    return icon;
  } catch (_) {
    /* empty */
  }
};

const NetIcon = (props: { name: string }) => {
  const [icon, setIcon] = useState(null);
  useEffect(() => {
    if (!props.name) {
      return;
    }
    const icSignal = new AbortController();
    fetchSvgIcon(props.name, { signal: icSignal.signal }).then((ic) => {
      if (!ic) {
        return;
      }
      const parser = new DOMParser();
      const svgDoc = parser.parseFromString(ic, "image/svg+xml");
      const svgElement = svgDoc.documentElement;
      const svgAttrs = svgElement.attributes;
      const reactAttrs = {
        dangerouslySetInnerHTML: { __html: svgElement.innerHTML },
      };

      for (let i = 0; i < svgAttrs.length; i++) {
        const attr = svgAttrs.item(i);
        reactAttrs[attr.nodeName] = attr.nodeValue;
      }

      setIcon(createElement(svgElement.tagName, reactAttrs));
    });
    return () => icSignal.abort();
  }, [props.name]);
  if (!props.name) return null;
  if (!icon) return <DownloadingLoopIcon />;
  return icon;
};

export default NetIcon;
